<template>
  <div class="slider">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="iframeBox">
          <iframe src="#/sliderExample"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">slider 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import slider from "cy_static/slider"; 
&lt;/script&gt;
</xmp >
  
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
  &lt;style type="text/css"&gt;
    #mySlider{width:100%;height:100px;position:relative;overflow:hidden;background-color:#d9d9d9}
    #mySlider .content{display:table;white-space:nowrap}
    #mySlider .content .l-item{display:inline-block;width:375px;position:relative;height:100px;line-height:100px;}
    #mySlider .navMenu{position:absolute;left:0;right:0;bottom:5px;padding:0px 0;display:inline-block;text-align:center}
    #mySlider .navMenu b{width:6px;height:6px;display:inline-block;background:#fff;margin:0 3px;border-radius:2px}
    #mySlider .navMenu b.curr{border-radius:9px;width:20px;background:#fff}      
  &lt;/style&gt;  
  &lt;div id="mySlider"&gt;
      &lt;div class="content"&gt;
          &lt;div class="l-item"&gt;轮播一&lt;/div&gt;
          &lt;div class="l-item"&gt;轮播二&lt;/div&gt;
          &lt;div class="l-item"&gt;轮播三&lt;/div&gt;
          &lt;div class="l-item"&gt;轮播四&lt;/div&gt;
          &lt;div class="l-item"&gt;轮播五&lt;/div&gt;
      &lt;/div&gt;
  &lt;/div&gt;
  &lt;script type="text/javascript"&gt;
      import slider from "cy_static/slider"; 
      slider({
          elem: "#mySlider", // 窗口元素--要设置高度
          content: ".content"
      });
  &lt;/script&gt;
</xmp > 
 
      </div>

      <h2 class="cy_h2">API</h2>      
      <div class="apiModule">
        <h2 class="cy_h2">事件</h2>
        <ul class="ul-props">
					<li>
						<p><a>scroll</a>() : <a>Void</a></p>
						<p>触发绑定在content滚动事件；</p>
					</li>
				</ul>
      </div>
      <div class="apiModule">
        <h2 class="cy_h2">构造方法</h2>				
            <p><a>slider</a> (params : Object) : <a>Object</a></p>
						<p>图片轮播，支持定位特定图片；</p>			
            <table class="table-props">
              <tr>
                <th>参数名</th>
                <th>类型</th>
                <th>描述</th>
                <th>默认值</th>
                <th class="text-c">必需</th>
              </tr>
              <tr>
                <td>elem</td>
                <td class="atn">String||Object</td>
                <td>窗口元素</td>
                <td>""</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>content</td>
                <td class="atn">String||Object</td>
                <td>窗口子元素</td>
                <td>""</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>time</td>
                <td class="atn">Number</td>
                <td>滑动时间(毫秒)</td>
                <td>550</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>drag</td>
                <td class="atn">Number</td>
                <td>阻尔糸数</td>
                <td>0.1</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>autoTime</td>
                <td class="atn">Number</td>
                <td>自动轮播(毫秒)</td>
                <td>0</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>moveInt</td>
                <td class="atn">Boolean</td>
                <td>移动整格</td>
                <td>true</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>isSpring</td>
                <td class="atn">Boolean</td>
                <td>启动弹性吸附效果</td>
                <td>true</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>isShowNum</td>
                <td class="atn">Number</td>
                <td>显示当前数字</td>
                <td>false</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>resetWidth</td>
                <td class="atn">Number</td>
                <td>重置复位宽度(PX)</td>
                <td>10</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>currCss</td>
                <td class="atn">String</td>
                <td>默认选中样式</td>
                <td>"curr"</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>currIndex</td>
                <td class="atn">Number</td>
                <td>默认选中的图片</td>
                <td>0</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>hasNavMenu</td>
                <td class="atn">Boolean</td>
                <td>拥有导航菜单</td>
                <td>true</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onStart</td>
                <td class="atn">Function</td>
                <td>拖动开始</td>
                <td>function (left, event, params) {}</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onScroll</td>
                <td class="atn">Function</td>
                <td>滚动事件</td>
                <td>function (left) {}</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onMove</td>
                <td class="atn">Function</td>
                <td>拖动中</td>
                <td>function (left, event, params) {}</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onStop</td>
                <td class="atn">Function</td>
                <td>拖动结束</td>
                <td>function (left, event, params) {}</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onChange</td>
                <td class="atn">Function</td>
                <td>图片定位改变事件</td>
                <td>function (index, dom) {}</td>
                <td class="text-c">N</td>
              </tr>
            </table> 
      </div>

      <div class="apiModule">
        <h2 class="cy_h2">实例方法</h2>
        <ul class="ul-props">
					<li>
						<p><a>pause</a>() : <a>Void</a></p>
						<p>禁止滑动；</p>
					</li>
					<li>
						<p><a>play</a>() : <a>Void</a></p>
						<p>启动滑动；</p>
					</li>		
					<li>
						<p><a>setActivated</a>(isActivated) : <a>Void</a></p>
						<p>是否激活；</p>
					</li>
					<li>
						<p><a>destroy</a>() : <a>Void</a></p>
						<p>销毁实例里所有事件；</p>
					</li>
					<li>
						<p><a>setCurrIndex</a>(index : Number) : <a>Void</a></p>
						<p>设置要选中图片下标（从0开始）；</p>
					</li>
				</ul>
      </div>




    </div> 
  </div>
</template>

<script>
export default {
  name: 'slider',
  data () {
    return {
      msg: 'slider',
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  }, 
  mounted(){
    var m = this;
    m.$refs.codeStyle1.codemirror.setValue(m.$refs.codeStyleVal1.innerHTML.replace(/^\s|\s+$/g,''));
    m.$refs.codeStyle2.codemirror.setValue(m.$refs.codeStyleVal2.innerHTML.replace(/^\s|\s+$/g,''));
  }
}
</script>    
<style scoped>
  .cy_example .iframeBox{position:absolute;top:43px;width:100%;bottom:0}
</style>


